
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Countup from 'vux/src/components/countup/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Icon from 'vux/src/components/icon/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import Alert from 'vux/src/components/alert/index.vue'

import CountDown from './plugins/CountDown.vue'
import CountDownse from './plugins/CountDownse.vue'
import AnswerInput from './plugins/AnswerInput.vue'


export default {
components: {
TransferDom,
XHeader,
XButton,
Box,
XInput,
Group,
Cell,
Toast,
Tabbar,
TabbarItem,
Flexbox,
FlexboxItem,
Divider,
Countup,
Swiper,
SwiperItem,
CountDown,
CountDownse,
XDialog,
Icon,
Confirm,
AnswerInput,
Alert
},
directives: {
TransferDom
},
created () {
this.todayQuestion()
},
mounted () {

this.$navigation.once('back', (to, from) => {
this.isAnswerToday = true

this.failureShowToast = false
this.changeSuccessShow = false

this.getUserInfo()
})
this.$navigation.once('forward', (to, from) => {
console.log(to, from)
console.log('que' + from.route.path)
if (from.route.path === '/questionhide' && (to.route.path === 'home' || to.route.path === '/personCenter')) {
this.onConfirmBack()
}
})

this.failureShowToast = false
this.changeSuccessShow = false

this.getUserInfo()
if (window.localStorage.clock2) {
this.clock = parseInt(window.localStorage.clock2)
}
},
beforeDestroy () {
window.localStorage.removeItem('clock2')
},
data () {
return {
title: '每日一题',
mobile: '',
password: '',
iconType: '',
successText: '',
changeSuccessShow: false,
changeFailureShow: false,
startCount: 0,
endCount: 100,
totalPages: 1002,
failureText: '',
clock: 0,  // answer question time range
blankNum: 0,
isLeave: false,
isReaded: false,
isAnswerToday: false,
isRealBack: false,
answerList: {
1: '',
2: ''
},
answerPeopleList: [
{name: 'Tony', getCoins: 20},
{name: 'Mike', getCoins: 20},
{name: 'Xiao', getCoins: 20},
{name: 'Tom', getCoins: 20}
],
mike0: '',
mike1: '',
mike2: '',
questionData: '<p align="center"><i class="fa fa-spinner"></i></p>',
questionId: '',
firstAnswer0: '',
firstAnswer1: '',
firstAnswer2: '',
firstAnswer3: '',
secondAnswer: '',
thirdAnswer: '',
submitBtnText: '提交答案',
noActive: true,
disableClass: '',
showTimeOut: false,
answerRightShow: false,
answerWrongShow: false,
deductibleCredits: 0,
increaseCredits: 0,
answerOverShow: false,
overText: '',
viewBoxshow: false,
timers: null,
newClock: 0
}
},
methods: {
goHomePage () {
this.$router.push('/home')
},
enableSubmit () {
for (let key in this.answerList) {
if (!this.answerList[key].trim()) {
this.noActive = true
break
} else {
this.noActive = false
}
}
},
onShow () {
},
onForwordHide () {
location.reload()
},
onHide () {
},
onHideEvent () {
},
cleanTimer (timer) {
console.log('timers:' + timer)
this.timers = timer
},
mainCount (newClock) {
console.log('newClock:' + newClock)
this.newClock = newClock
},
onFailureHide () {

},
onSuccessHide () {
this.$router.push('/home')
},
todayQuestion () {
this.$http.post(this.$apis.todayQuestion, {isToday: true}).then(res => {
if (res.code === 200) {
this.questionId = res.data._id
if (window.localStorage.clock2) {
this.clock = parseInt(window.localStorage.clock2)
} else {
this.clock = res.data.clock 
}
this.blankNum = res.data.blankNum

let answerList = {}
for (let i = 0; i < this.blankNum; i++) {
answerList[i + 1] = ''
}
this.answerList = answerList
this.questionData = res.data.sentence
this.deductibleCredits = res.data.deductibleCredits
this.increaseCredits = res.data.increaseCredits
} else {
this.questionData = '<p>Today\'s question is not ready, please wait for a minute!</p>'
}
}).catch(function (error) {
console.log('Error: ' + error)
})
},
submitAnswer () {
clearInterval(this.timers)  
this.$vux.loading.show({
text: '审核中...'
})
let userId = window.localStorage.getItem('userId')

let answerArr = []
for (let key in this.answerList) {
answerArr.push(this.answerList[key].trim())
}

this.$http.post(this.$apis.answer, {
isToday: true,
answer: answerArr,
questionId: this.questionId,
userId: userId
}).then(res => {
this.$vux.loading.hide()
if (res.code === 200) {
this.increaseCredits = res.data.increaseCredits
this.answerRightShow = true
} else if (res.code === 202) {  // wrong answer
this.deductibleCredits = res.data.deductibleCredits
this.answerWrongShow = true
} else {  // already answer
this.answerOverShow = true
this.overText = res.msg
}
localStorage.removeItem('clock2')
})

},
onConfirmBack () { // 离开自动放弃答题
clearInterval(this.timers)  
window.localStorage.removeItem('clock2')

let userId = window.localStorage.getItem('userId')
this.$http.post(this.$apis.answerTimeout, {userId: userId, questionId: this.questionId, timeout: true})
.then((res) => {
this.submitBtnText = '放弃答题!'
this.disableClass = 'disable-btn'
this.noActive = true
this.isRealBack = true
})
.catch(error => {
console.log(error)
})
},
timeOutFun () {
console.log('倒计时结束！')
window.localStorage.removeItem('clock2')

let userId = window.localStorage.getItem('userId')
this.$http.post(this.$apis.answerTimeout, {userId: userId, questionId: this.questionId, timeout: true})
.then((res) => {
this.submitBtnText = '回答时间到!'
this.disableClass = 'disable-btn'
this.noActive = true
this.showTimeOut = true
this.flag = true
})
.catch(error => {
console.log(error)
})

},
onIndexChange (newIndex, oldIndex) {
console.log(newIndex, oldIndex)
},
change (val) {
console.log('on change', val)
},
onBlur (val) {
console.log('on blur', val)
},
onFocus (val, $event) {
console.log('on focus', val, $event)
},
onEnter (val) {
console.log('click enter!', val)
},
onCancel () {
},
onCancelback () {
this.$router.push('home')
},
onConfirm () {
this.$router.push('questionlist')
},
getUserInfo () {
let userId = window.localStorage.getItem('userId')
this.$http.post(this.$apis.userInfo, {userId: userId})
.then((res) => {
this.isAnswerToday = res.data.isAnswerToday
})
.catch(function (error) {
console.log(error)
})
}
}
}



